<template>
<div class="edit">
  <div class="edit-header">
    <div class="backto">
        <span><router-link to="/detail">< 我的博客</router-link></span>
    </div>
    <div class="edit-search">
      <label for=""></label>
      <input type="text" placeholder="输入文章的标题">
    </div>  
    <div class="button">
       <button>发布文章</button>
    </div> 
     
  </div>
  <mavon-editor class="mavon"
    ref="editor"
    v-model="doc">
  </mavon-editor>
  <div class="checkboxes">
     <h2>文章类型 :</h2>
     <label for="">vue.js</label>
     <input type="checkbox"  value="vue.js" v-model="blog.categories">
     <label for="">react.js</label>
     <input type="checkbox"  value="react.js" v-model="blog.categories">
     <label for="">node.js</label>
     <input type="checkbox" value="node.js"  v-model="blog.categories">
     <label for="">auguar.js</label>
     <input type="checkbox" value="auguar.js" v-model="blog.categories">
  </div>
  <div class="describe">
       <label for="">文章说明 :</label>
        <textarea v-model="blog.content"></textarea>
  </div>
</div>



</template>

<script>

import "../../../../node_modules/mavon-editor/dist/css/index.css"

export default {
  name: 'edit',
  data () {
    return {
      blog:{
          title:"",
          content:"",
          categories:[],
          authod:"",     
      },
      submited:false,
    }
  },
  methods:{
    
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.edit{
    width:960px;
    margin:0 auto;
    padding:0 12px;
    box-sizing: border-box;
}
.edit *{
  box-sizing: border-box;
}

.edit-header{
  display:flex;
  width:90%;
  height:36px;
  align-items: center;
  flex-direction: row;
}

.edit-header .backto{
  width:90px;
  min-width:80px; 
}
.edit-header .edit-search{
  display: flex;
  width: 100%;
  min-width:200px;
  margin: 8px 16px 8px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff
}
.edit-header .edit-search input{
  width:100%;
  height:22px;
  
}
.edit-header .button{
  flex-direction: row;
  padding-left: 24px;
  display: flex;
  width:90px;
  min-width:80px;
}
.edit-header a{
  text-decoration: none;
  font-size:13px;
  /* font-weight: blod;
  color:-webkit-link; */
}

.edit-header button{
  font-size:13px;
}

.mavon{
  height:500px;
}

.checkboxes{
    margin:20px 0 10px 0;
}

.checkboxes h2{
    display:inline-block;
}

.checkboxes label{
    display: inline-block;
    margin-top:0;
    font-size:16px;
    padding-left:10px;
}

.checkboxes input{
    display:inline-block;
    margin-right:10px;
}
.describe label{
    font-size:14px;
    display: block;
}

.describe textarea{
    display: block;
    width:380px;
    height:125px;
    margin:10px 0 0 36px;
    
}

</style>



