<template>
  <div class="show-component">
    <div class="edit-header">
      <div class="backto">
          <span><router-link to="/detail">< 我的博客</router-link></span>
      </div>
    </div>
    
    <div class="content">
       <h1>{{blog.title}}</h1>  
        <article class="markdown-body" style="text-align:left" v-html="content"></article> 
    </div> 
    
   

  </div>
</template>

<script>

import '../../../../node_modules/github-markdown-css/github-markdown.css'
import adpp from '../../../../static/dapp.md'
import marked from 'marked'

export default {
  
  name: 'ShowComponent',
  data () {
    return {
      id:this.$route.params.id,
      blog: {},
      content:"",
    }
  },
  created(){
    this.$http.get("../../../../static/blog.json")
            .then(function(res){
                 //console.log(res.body);
                let bloglists=res.body.bloglists;
                let preblog =bloglists.filter((ele)=>{
                  return ele.Id==this.id;
                })
                this.blog=preblog[0];
                console.log(this.blog)
                return preblog[0];
            })
            .then(function(blog){ 
              const url=blog.content;
              console.log('../../../../static/' + url)
              let content = require("../../../../static/"+url)
              this.content = marked(content)                 
            })
            
            
  },
 
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.show-component{
        width:960px;
        max-width:960px;
        margin:0 auto;
        /* padding:0 20px; */
        /* background: #eee; */
        /* border:1px dotted #aaa; */
        box-sizing: border-box;
        
    }

.edit-header{
  display:flex;
  width:90%;
  height:36px;
  /* align-items: center; */
  flex-direction: row;
}    

.edit-header .backto{ 
  padding:10px 0 5px 12px;
}

.edit-header a{
  text-decoration: none;
  font-size:13px;
  /* font-weight: blod; */
  /* color:-webkit-link; */
}




.content{
  margin:0 auto;
  width:100%;
  padding:0;
  box-sizing: border-box;
  margin-top:-7px;

}



.show-component h1{
  width:100%;
  font-size:28px;
  padding-left:12px;
  margin-bottom:10px;
  /* text-align: center; */
  font-family: 'Microsoft YaHei','SF Pro Display',Roboto,Noto,Arial,'PingFang SC',sans-serif;;
}

/* makedown的样式 */
.markdown-body {   
  box-sizing: border-box;
  min-width: 200px;
  /* max-width: 980px; */
  /* padding: 45px; */
  max-width: 98%;
  margin: 0 auto;
  box-shadow: 2px 4px 6px gray;
  padding-left: 20px;
  padding-right: 15px;
  padding-top: 40px;
  padding-bottom: 45px;
  margin-bottom: 100px
}

.markdown-body {
    box-sizing: border-box;
    min-width: 200px;
    max-width: 980px;
    margin: 0 auto;
    padding: 45px;
}
 
@media (max-width: 767px) {
    .markdown-body {
        padding: 15px;
    }
}


</style>


