<template>
  <div class="blog-content">
    <div class="header">
        <div class="header-title">
            <span><a href="#">我的博客</a> </span> > <span><a href="#">博客目录</a> </span>
        </div>
        <div class="search">
            <input type="text" placeholder="搜索"  v-model="search"> <button>找找看</button>
        </div>  
    </div>
    <div class="blog-exhition">
        <div class="single-blog" v-for="blog in filterBlogs">
        <router-link v-bind:to="'/detail/show/'+blog.Id">
            <div class="title">
                <span class="create-type">原创</span> <h2>{{blog.title | to-uppercase}}</h2>
            </div>
            <article>{{blog.describe}}</article>
            <div class="blog-describe">
                <span class="create-date">{{blog.date}}</span>
                <span class="blog-category">文章类型 : {{blog.category}}</span>   
            </div>
            
        </router-link>    
        
    </div>
    <div class="categories-exhibition">
        <h3 >分类专栏</h3>
        <div class="categories-content">
            <ul>
                <li class="typeitem" v-for="item in Categories">
                    <a class=""  href="#">
                        <span class="catename">{{item.catename}}</span>
                        <span class="catenum">{{item.catenum}}篇</span>
                        <div class="clear"></div>
                    </a>
                </li>
                            
            </ul>
        </div>
        
        

    </div>
        
    </div>  
    
  </div>
</template>

<script>
import dapp from "../../../../static/dapp.md";

export default {
  name: 'detail-content',
  data () {
    return {
        Blogs:[],
        Categories:[{catename:"html+css",catenum:1},
                    {catename:"js",catenum:1},
                    {catename:"webapi",catenum:1},
                    {catename:"vue",catenum:2},
                    {catename:"node",catenum:1},
                    {catename:"后端ajax",catenum:1},
                    {catename:"es6接触",catenum:1},
        ],
        search:"",
     }
  },
  computed:{
      filterBlogs:function(){
          return this.Blogs.filter((blog)=>{
                var reg=eval("/"+this.search+"/gi")
                return blog.title.match(reg);  //filter中的函数要是返回布尔值的；
         })
        }
  },
  created:function(){
      this.$http.get('../../../../static/blog.json')
                .then(function(res){
                    // console.log(res.body.);
                    let bloglists=res.body.bloglists;
                    //console.log(instanceof )
                    for(let blog of bloglists){
                        // let url=blog.content
                        // let content= import("../../../../static/"+url)
                        // blog.content=content
                        this.Blogs.push(blog);
                    }
                    console.log(this.Blogs)
                })


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .blog-content{
        padding-left:10px;
        position: relative;
        width:960px;
        min-height: 360px;
        margin:0 auto;
        box-sizing: border-box;

    }
    .header{
        height:36px;
        margin-bottom:1px;
        padding:2px;
    }
    .header-title{
        float:left;
        line-height:36px;
    }
    .header-title a{
        font-size:13px;
        font-weight: blod;
        text-decoration: none !important;
    }
    .search{
        margin-top:2px;
        float:right;
        padding:5px 290px 0 0;
    }
    .search input{
        height:18px;
    }
    
    /* 博客正文部分 */

    .blog-exhition{
        width:645px;
    }

    .blog-exhition a{
        display: block;
        width:100%;
    }
    .single-blog{
        border-top: 1px solid #d7dae0;
        border-bottom:1px solid #d7dae0;
        margin-bottom:10px;
        box-shadow: 0 -3px 5px rgba(0,0,0,0.06);
        padding-left:10px;
    }
    
    .single-blog .create-type{
        color: #e33e33;
        background-color: rgba(227,62,51,0.1);
        box-sizing: border-box;
        display: inline-block;
        margin-bottom:8px;
        /* width: 34px;
        height: 20px; */
        /* line-height: 20px; */
        text-align: center;
        font-size: 12px;
        border-radius: 2px;
        vertical-align: middle;
    }
    .single-blog a{
        display: inline-block;
        padding:5px;
        text-decoration: none;
        padding:0;
        
    }
    .single-blog a h2{
        font-size:20px ;
        font-weight: blod;  
        font-family: 'Microsoft YaHei'; 
        color:black;
        display:inline-block;
    }
    .single-blog a:hover h2{  
        color:#015fb6;
    }
    .single-blog article{
        color:#ccc;
        font-size:18px;
        font-weight: normal;
        font-family: 'Microsoft YaHei';
        margin-bottom:5px;

        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        
    }
    

    .blog-describe{
       color: #5f6471;
       font-size:14px;
       margin-bottom:4px;
    }
    .blog-category{
        float:right;
        color: #e33e33;
        font-size: 14px;
        padding-right:20px;
    }


    .categories-exhibition{
        width:236px;
        position:absolute;
        top:45px;
        right:15px;
    }

    .categories-exhibition h3{
        color: #3d3d3d;
        background: linear-gradient(#efefef,#ddd);
        background-size: 300px 38px;
        padding: 0 18px;
        height: 38px;
        line-height: 38px;
        font-size:17px;
        font-weight: bold;
    }
    .categories-content .typeitem{
        padding: 0 20px;
    }
    .categories-content .typeitem a{
        text-decoration: none;
        font-size:15px;
        font-family: 'Microsoft YaHei';
        
    }
    .typeitem{
        height:30px;
        line-height: 30px;
        border-bottom: 1px solid #d7dae0;
        box-shadow: 0 -3px 5px rgba(0,0,0,0.06);
    }

    .typeitem a  .catename{
        float:left;
        padding-left:10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    } 
    .typeitem a  .catenum{
        float:right;
        padding-right:8px;
    } 

</style>


