import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import Home from '../pages/home/Home'
import Detail from '../pages/detail/Detail'
import AddBlog from '../pages/detail/AddBlog'
import ShowBlog from '../pages/detail/ShowBlog.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,

    },{
      path: '/detail',
      name: 'Detail',
      component: Detail
    },{
      path: '/detail/add',
      name: 'AddBlog',
      component: AddBlog
    },{
      path: '/detail/show/:id',
      name: 'ShowBlog',
      component: ShowBlog
    }
  ],
  mode:"history",
})



